table {
    border: 1px solid rgb(0, 0, 0);
    border-radius: 3px;
    margin: auto;
    padding: 1.3rem;
    text-align: center;
}

table thead tr th {
    border: 1px solid #ccc;
    padding: .3rem .6rem;
}

table tbody tr td {
    padding: .5rem 1rem;
}

table tbody tr td button {
    outline: none;
    border: none;
    cursor: pointer;
    padding: .2rem .4rem;
    font-size: .7rem;
    letter-spacing: .05rem;
}

table tr td button:nth-child(1){
    background: rgb(0, 255, 21);
    color: #fff;
}

table tr td button:nth-child(2){
    margin-left: .3rem;
    background: rgb(255, 1, 1);
    color: #fff;
}