
.form-modal {
    position: absolute;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    top: -34px;
    left: -34px;
    background: rgba(0,0,0,.5);
    z-index: 100;
}

.container-modal {
    border: 2px solid #000;
    padding: 1.5rem;
    border-radius: 5px;
    margin-top: -300px;
    width: 30%;
    background: #fff;
}


.container-modal__inputs {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.container-modal__inputs input {
    padding: .7rem;
    line-height: .7rem;
    border-radius: 3px;
    border: 1px solid #ccc; 
    outline: none;
}

.container-modal__btns {
    margin-top: .8rem;
    display: flex;
    justify-content: space-around;
}

.container-modal__btns button {
    padding: .5rem 1rem;
    border-radius: 5px;
    border: none;
    outline: none;
    font-size: 1rem;
    cursor: pointer;
    color: #fff;
}

.container-modal__btns button:nth-child(1){
    background: rgb(3, 187, 3);
}

.container-modal__btns button:nth-child(2) {
    background: rgb(228, 2, 2);   
}
