form {
    display: flex;
    margin: 2rem;
    justify-content: center;
    align-items: center;
}

form .container-input {
    display: flex;
    flex-direction: column;
    width: 40%;
}

form .container-input input {
    padding: .6rem;
    line-height: .6rem;
    border-radius: 3px;
    border: 1px solid #ccc;
    outline: none;
}

form .container-input input:nth-child(2) {
    margin-top: 1rem;
}

form .container-input p {
    color: red;
}

.container-btn {
    margin-left: 2rem;
}

.container-btn button {
    padding: .6rem;
    background: rgb(3, 163, 237);
    border: none;
    outline: none;
    color: #fff;
    border-radius: 3px;
    cursor: pointer;
    font-size: 1rem;
    letter-spacing: .2rem;
}

.container-btn button:hover {
    background: rgba(3, 163, 237, 0.7);
}